.appContainer {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: black;
    z-index: 2;
    color: white;
}

.apps {
    margin: 15vh auto 0vh;
    width: 30%;
    border-radius: 25px;
    z-index: 2;
    max-width: 950px;
    overflow: hidden;
}

.apps img {
    width: 100%;
    height: 100%;
    z-index: 6;
}

.appNav {
    width: 80%;
    margin: 5vh auto 0;
    height: 7vh;
    display: flex;
    z-index: 6;
    justify-content: space-around;
    align-items: center;
    border-radius: 8px;
}

.appNav button {
    width: 15%;
    overflow: hidden;
    border-radius: 12px;
    border: 3px solid transparent;
    padding: 5px;
    background: black;
}

.appNav button:hover {
    /* border: 3px solid red; */
    transform: scale(1.5);
    cursor: pointer;
}

.appNav button:focus {
    border: 1px solid red;
    border-radius: 0;
    cursor: pointer;
}

.appNav img {
    width: 100%;
    height: 100%;
    border: 4px solid black;
    border-radius: 5px;
    position: relative;
    z-index: 6;
    border: 2px solid lightgray;
}

.infoContainer {
    width: 40%;
    margin: 5vh auto 20vh;
    border-radius: 8px;
    overflow: hidden;
    height: 20vh;
}

.info {
    display: flex;
    width: 100%;
    height: 100%;
    text-align: center;
    color: cyan;
    flex-wrap: wrap;
    z-index: 6;
    font-family: "Roboto", sans-serif;
}

.info h1 {
    font-size: 2.5vw;
    width: 100%;
    margin-top: 0;
}

.info p {
    font-size: 2vw;
    width: 100%;
    margin: 0;
}

.info a {
    font-size: 1.5vw;
    width: 50%;
    color: cyan;
}

.portfolioTitle {
    background-image: 
    linear-gradient(to right, lightblue,white, lightblue); 
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;  
    animation: rainbow-animation 35s linear infinite;
}

@keyframes rainbow-animation {
    to {
        background-position: 400vh;
    }
}